// apps/web/src/main.tsx
//  :    (  Telegram)

import React from 'react';
import * as ReactDOM from 'react-dom/client';
import ErrorBoundary from './components/ErrorBoundary';
import i18n from './i18n';

//   
import './index.css';

import { ensureWebpackNonce } from './utils/ensureWebpackNonce';

(
  globalThis as { __ERM_IMPORT_META_ENV__?: ImportMetaEnv }
).__ERM_IMPORT_META_ENV__ = (
  import.meta as ImportMeta & { env?: ImportMetaEnv }
).env;

ensureWebpackNonce();

function bootstrap() {
  const root = document.getElementById('root');
  if (!root) {
    throw new Error('Element #root not found');
  }

  const params = new URLSearchParams(window.location.search);
  const forceBrowser = params.get('browser') === '1';

  const webApp = (window as any).Telegram?.WebApp;
  const supportedPlatforms = ['android', 'ios', 'web', 'macos', 'tdesktop'];
  const minVersion = '6.0';

  function versionAtLeast(current: string, min: string) {
    const a = current.split('.').map(Number);
    const b = min.split('.').map(Number);
    for (let i = 0; i < Math.max(a.length, b.length); i++) {
      const x = a[i] || 0;
      const y = b[i] || 0;
      if (x > y) return true;
      if (x < y) return false;
    }
    return true;
  }

  let isTelegram = false;
  if (!forceBrowser && webApp) {
    isTelegram =
      supportedPlatforms.includes(webApp.platform) &&
      versionAtLeast(webApp.version, minVersion);
    if (!isTelegram) {
      (window as any).__ALERT_MESSAGE__ =
        '  Telegram.   .';
    }
  }

  function render(Component: React.ComponentType) {
    ReactDOM.createRoot(root).render(
      <ErrorBoundary
        fallback={
          <div>
            {i18n.t(
              'errorFallback',
              '-   .   (Ctrl+R  +R).',
            )}
          </div>
        }
      >
        <React.StrictMode>
          <Component />
        </React.StrictMode>
      </ErrorBoundary>,
    );
  }

  if (isTelegram) {
    import('./TelegramApp')
      .then(({ default: TelegramApp }) => render(TelegramApp))
      .catch((e) => console.error('Failed to load TelegramApp', e));
  } else {
    import('./App')
      .then(({ default: App }) => render(App))
      .catch((e) => console.error('Failed to load App', e));
  }
}

if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', bootstrap);
} else {
  bootstrap();
}
